@import url('https://fonts.googleapis.com/css?family=Oswald|Source+Sans+Pro');

html, body {
  min-height: 100%;
  margin: 0;
}
body {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  text-align: center;
  font-family: 'Source Sans Pro';
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin-block-start: 0;
  margin-block-end: 0;
}
